.title-img{
    width:100%;
    height:100%;
    margin:0;
}

.content-place{
    padding: 2em;
}

.svg-container{
    width:100%;
    height:330px;
}

.title-logo {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
}

.title-logo img{
    margin-left:auto;
    margin-right:auto;
}

.welcome-h1{
    margin-bottom:0;
}

.welcome-h4{
    margin-top:0;

}

.u-svg{
    -ms-transform:  translate(20rem, 7rem); /* IE 9 */
    -webkit-transform:  translate(20rem, 7rem); /* Safari */
    transform: translate(20rem, 7rem);
    -webkit-animation-duration:1s;
            animation-duration:1s;
    -webkit-animation-name:move-u;
            animation-name:move-u;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.i-svg{
    -ms-transform: translate(20rem, 7rem); /* IE 9 */
    -webkit-transform: translate(20rem, 7rem); /* Safari */
    transform: translate(20rem, 7rem);
    -webkit-animation-duration:1s;
            animation-duration:1s;
    -webkit-animation-name:move-i;
            animation-name:move-i;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

}

.app-svg{
    -ms-transform: translate(50px, 100px); /* IE 9 */
    -webkit-transform: translate(50px, 100px); /* Safari */
    transform: translate(16rem, -500px);
    -webkit-animation-duration:1s;
            animation-duration:1s;
    -webkit-animation-delay:0.4s;
            animation-delay:0.4s;
    -webkit-animation-name:move-app;
            animation-name:move-app;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes move-u{
    0%{
        -webkit-transform: translate(-20px, 7rem);
                transform: translate(-20px, 7rem);
    }
    99%{
        fill:#00966e;
    }
    100%{
        -webkit-transform: translate(20.6rem, 7rem);
                transform: translate(20.6rem, 7rem);
        fill:white;

    }
}

@keyframes move-u{
    0%{
        -webkit-transform: translate(-20px, 7rem);
                transform: translate(-20px, 7rem);
    }
    99%{
        fill:#00966e;
    }
    100%{
        -webkit-transform: translate(20.6rem, 7rem);
                transform: translate(20.6rem, 7rem);
        fill:white;

    }
}

@-webkit-keyframes move-i{
    0%{
        -webkit-transform: translate(50em, 7rem);
                transform: translate(50em, 7rem);
    }
    99%{
        fill:#00966e;
    }
    100%{
        -webkit-transform: translate(20.6rem, 7rem);
                transform: translate(20.6rem, 7rem);
        fill:white;

    }
}

@keyframes move-i{
    0%{
        -webkit-transform: translate(50em, 7rem);
                transform: translate(50em, 7rem);
    }
    99%{
        fill:#00966e;
    }
    100%{
        -webkit-transform: translate(20.6rem, 7rem);
                transform: translate(20.6rem, 7rem);
        fill:white;

    }
}

@-webkit-keyframes move-app{
    0%{
        -webkit-transform: translate(16rem, -500px);
                transform: translate(16rem, -500px);
    }

    100%{
        -webkit-transform: translate(16rem, 4rem);
                transform: translate(16rem, 4rem);
    }
}

@keyframes move-app{
    0%{
        -webkit-transform: translate(16rem, -500px);
                transform: translate(16rem, -500px);
    }

    100%{
        -webkit-transform: translate(16rem, 4rem);
                transform: translate(16rem, 4rem);
    }
}


